

export default { //   
  // bind update
  clickOutside: {
    inserted(el, bindings) { // eldom
      document.addEventListener('click', (e) => {
        if (e.target === el || el.contains(e.target)) {
          return;
        }
        bindings.value(); // close
      });
    },
    // unbind() { //  
    //   //   document.removeEventListener('click', listener);
    // },
  },
};
