<template>
    <div class="cascader" v-click-outside="close">
        <div class="title" @click="toggle">
            {{result}}
        </div>
        <div  v-if="isVisible">
            <CascaderItem :options="options" :value="value" :level="0" @change="change"></CascaderItem>
            <!--  -->
        </div>
    </div>
</template>
<script>
//  cascader
// 

//   
// click-outside

// export default 
// export xxx 
import util from '../directives/clickOutside';
import CascaderItem from './CascaderItem';
import cloneDeep  from 'lodash/cloneDeep'
export default {
    components:{
        CascaderItem
    },
    props:{
        lazyload:{
            type:Function
        },
        value:{
            type:Array,
            default:()=>[]
        },
        options:{
            type:Array,
            default:()=>[]
        }
    },
    directives:{
        clickOutside:util.clickOutside,
    },
    computed:{
        result(){
            return this.value.map(item=>item.label).join('/')
        }
    },
    methods:{
        handle(id,children){
            let cloneOptions = cloneDeep(this.options);
            //   
            // id 
            let stack = [...cloneOptions];
            let index = 0;
            let current;
            while(current = stack[index++]){ // 
                if(current.id!==id){
                    if(current.children){
                        stack = stack.concat(current.children);
                    }
                }else{
                    break;
                }
            }
            if(current){ //   
                current.children = children; // 
                this.$emit('update:options',cloneOptions)
            }
        },
        change(value){
            //  lazyload
            let lastItem = value[value.length-1];
            let id = lastItem.id;
            if(this.lazyload){
                //  id  children
                this.lazyload(id,(children)=>this.handle(id,children));
            }
            this.$emit('input',value);
        },
        close(){ // 
            this.isVisible = false;
        },
        toggle(){ // 
            this.isVisible = !this.isVisible;
        }
    },
    data(){
        return {isVisible:false}
    }
}
</script>
<style lang="stylus">
.cascader
    display inline-block;
.title 
    width 150px;
    height 30px;
    border 1px solid #ccc;
.content
    display flex
.content-left
    border:1px solid #ccc;
    min-height:150px;
    max-height:150px;
    overflow-y scroll;
.label
    width 80px;
    padding-left 5px;
.label:hover
    background:#999;
    cursor pointer
.title
    line-height 30px;
</style>